<template>
  <div class="p-4 bg-purple-100 weather-screen">
    <!-- Hidden select dropdown -->
    <div
      class="fixed top-0 left-0 h-screen w-screen bg-transparent z-10 transition-all duration-500 ease-in-out"
      :class="{ '-translate-y-full': !showSelect }"
    >
      <div
        class="bg-white p-4 rounded-b-lg shadow-md w-full"
        @click="showSelect = false"
      >
        <select
          class="w-full p-2 border border-gray-300 rounded-md"
          v-model="selectedCity"
        >
          <option v-for="city in coordinates" :key="city.city" :value="city">
            {{ city.city }}
          </option>
        </select>
      </div>
    </div>

    <!-- Trigger -->
    <button
      class="text-gray-500 absolute top-0 right-0 mt-4 mr-4"
      @click="showSelect = !showSelect"
    >
      <MagnifyingGlassIcon class="h-6 w-6" />
    </button>
    <!-- Header -->
    <div class="flex justify-between items-center">
      <div>
        <p class="text-lg font-semibold">{{ city }}</p>
        <p class="text-5xl font-bold mt-2">{{ temperature }}°</p>
        <p class="text-sm">{{ day }}</p>
        <div v-for="dis in disaster" :key="dis">
            <span class="text-sm mr-2">{{dis.disaster}}</span> <span>Severity: {{dis.severity}}</span>
        </div>
      </div>
      <div>
        <img
          :src="`https://www.weatherbit.io/static/img/icons/${icon}.png`"
          class="w-20 h-20"
        />
      </div>
    </div>

    <!-- Tabs
    <div class="flex space-x-4 mt-6">
      <button class="px-4 py-2 bg-white rounded-full shadow-md">Today</button>
      <button class="px-4 py-2 bg-white rounded-full shadow-md">
        Tomorrow
      </button>
      <button class="px-4 py-2 bg-purple-300 rounded-full shadow-md">
        10 days
      </button>
    </div> -->

    <!-- Forecast List -->
    <div class="mt-6 space-y-4">
      <!-- Daily Forecast Card -->
      <div
        class="p-4 bg-purple-200 rounded-lg flex justify-between items-center"
        v-for="day in otherDays"
        :key="day.datetime"
      >
        <div>
          <p class="text-sm font-semibold">{{ day.day }}</p>
          <p class="text-xs">{{ day.weather.description }}</p>
        </div>
        <div class="flex items-center space-x-2">
          <p class="text-lg font-semibold">{{ day.temp }}°</p>
          <img
            :src="`https://cdn.weatherbit.io/static/img/icons/${day.weather.icon}.png`"
            alt="Weather Icon"
            class="w-10 h-10"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, watch } from "vue";
import coordinates from "@/components/Coordinates";
import { MagnifyingGlassIcon } from "@heroicons/vue/24/solid";

const userLocation = ref(null);
const apiKey = process.env.VUE_APP_API_KEY;
const city = ref("");
const temperature = ref("");
const day = ref("");
const icon = ref("");
const disaster = ref(null);
const today = ref([]);
const otherDays = ref([]);
const showSelect = ref(false);
const selectedCity = ref(null);

watch(selectedCity, () => {
  displayForecast(selectedCity.value);
});

onMounted(() => {
  displayForecast(coordinates[0]);
});

const dayOfWeek = (dateStr) => {
  const date = new Date(dateStr);

  const options = { weekday: "long", month: "short", day: "numeric" };
  const formattedDate = date.toLocaleDateString("en-US", options);
  return formattedDate;
};

const displayForecast = (coord) => {
  if (!coord) return;

  fetch(
    `https://api.weatherbit.io/v2.0/forecast/daily?lat=${coord.latitude}&lon=${coord.longitude}&key=${apiKey}`
  )
    .then((res) => res.json())
    .then((data) => {
      city.value = data.city_name;
      temperature.value = data.data[0].temp;
      day.value = dayOfWeek(data.data[0].datetime);
      icon.value = data.data[0].weather.icon;
      disaster.value = predictDisaster(data.data[0]);
      otherDays.value = data.data.slice(1);

      let arr = [];

      otherDays.value.forEach((day) => {
        arr.push({
          datetime: day.datetime,
          temp: day.temp,
          day: dayOfWeek(day.datetime),
          weather: day.weather,
        });
      });
      otherDays.value = arr;
    });
};

function predictDisaster(weatherData) {
  const predictions = [];

  // Heatwave Prediction
  if (weatherData.app_max_temp > 40 && weatherData.uv >= 10) {
    predictions.push({
      disaster: "Heatwave",
      severity: "High",
      probability: "80-100%",
    });
  } else if (
    weatherData.app_max_temp >= 35 &&
    weatherData.app_max_temp <= 40 &&
    weatherData.uv >= 8
  ) {
    predictions.push({
      disaster: "Heatwave",
      severity: "Moderate",
      probability: "50-80%",
    });
  } else if (
    weatherData.app_max_temp >= 30 &&
    weatherData.app_max_temp < 35 &&
    weatherData.uv >= 6
  ) {
    predictions.push({
      disaster: "Heatwave",
      severity: "Low",
      probability: "20-50%",
    });
  }

  // Flood Prediction
  if (
    weatherData.pop >= 70 &&
    weatherData.precip >= 20 &&
    weatherData.rh >= 85
  ) {
    predictions.push({
      disaster: "Flood",
      severity: "High",
      probability: "80-100%",
    });
  } else if (
    weatherData.pop >= 50 &&
    weatherData.pop < 70 &&
    weatherData.precip >= 10 &&
    weatherData.rh >= 75
  ) {
    predictions.push({
      disaster: "Flood",
      severity: "Moderate",
      probability: "50-80%",
    });
  } else if (
    weatherData.pop >= 30 &&
    weatherData.pop < 50 &&
    weatherData.precip >= 5 &&
    weatherData.rh >= 65
  ) {
    predictions.push({
      disaster: "Flood",
      severity: "Low",
      probability: "20-50%",
    });
  }

  // Storm Prediction
  if (weatherData.wind_spd >= 20 || weatherData.wind_gust_spd >= 25) {
    predictions.push({
      disaster: "Storm",
      severity: "High",
      probability: "80-100%",
    });
  } else if (weatherData.wind_spd >= 15 && weatherData.wind_spd < 20) {
    predictions.push({
      disaster: "Storm",
      severity: "Moderate",
      probability: "50-80%",
    });
  } else if (weatherData.wind_spd >= 10 && weatherData.wind_spd < 15) {
    predictions.push({
      disaster: "Storm",
      severity: "Low",
      probability: "20-50%",
    });
  }

  // Drought Prediction
  if (
    weatherData.precip < 2 &&
    weatherData.app_max_temp > 35 &&
    weatherData.rh < 50
  ) {
    predictions.push({
      disaster: "Drought",
      severity: "High",
      probability: "80-100%",
    });
  } else if (
    weatherData.precip >= 2 &&
    weatherData.precip < 5 &&
    weatherData.app_max_temp >= 30 &&
    weatherData.app_max_temp <= 35 &&
    weatherData.rh < 60
  ) {
    predictions.push({
      disaster: "Drought",
      severity: "Moderate",
      probability: "50-80%",
    });
  } else if (
    weatherData.precip >= 5 &&
    weatherData.precip < 10 &&
    weatherData.app_max_temp >= 25 &&
    weatherData.app_max_temp < 30 &&
    weatherData.rh >= 60
  ) {
    predictions.push({
      disaster: "Drought",
      severity: "Low",
      probability: "20-50%",
    });
  }
  return predictions;
}
</script>

<style scoped>
.weather-screen {
  height: 95%;
}
</style>